@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-image: url(/Jup4308/static/media/lighthouse.0756965f.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  grid-gap: 5%;
  gap: 5%;
  vertical-align: middle;
  overflow: hidden;
}

.slider-container{
  width: 90vw;
  height: 55vh;
  background-color: rgba(0, 0, 0, 0.452);
  display: flex;
  flex-direction: row;
  margin-left: 2.5%;
  margin-right: 2.5%;
  padding: 2%;
}

.info-container{
  min-width: 50vw;
}

.footer-container{
  width: 100vw;
  height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-left: 2%;
  padding-bottom: 1%;
}

.footer-container ul{
  padding: 0;
  margin: 0.3rem;
  color: white;
  font-family: 'Quicksand';
}
/**
 * Images a now responsive
 */
 img {
	max-width: 100%;
	height: auto;
}

/**
 * Limit the container in
 * width for big screens
 */
 .container {
	width: 90vw;
	max-width: 100%;
	padding: 20px;
	margin: 0 auto;
    align-items: center;

}


/**
 * By using display: flex
 * Logo and nav are in 2 cols
 * align-items make them
 * vertically centered
 * justify-content distribute
 * horizontal spaces around
 * and flex-wrap break the
 * things in two lines in
 * small screens
 */
.main-header .container {
	display: flex;
	align-items: center;
	justify-content: space-around;
	flex-wrap: wrap;
}

/**
 * The followings are to
 * make things more
 * clean and airy
 * and contents centered
 */
.main-nav ul {
	text-align: center;
    color: black;
    font-family: 'Quicksand';
    font-size: x-large;
    font-weight: 400;
    margin: auto;
    padding: none;
    justify-content: space-around;
}

.main-nav-list{
    display: flex;
    padding-right: 15%;
}
.main-nav li {
	display: inline;

}
.main-nav a {
	display: inline-block;
    text-decoration: none;
}

/**
 * Space-around become
 * Space-between to
 * distribute space
 * between the flex-items
 */
 @media (min-width: 960px) {
	.main-header .container {
		justify-content: space-around;
	}
}

@media (max-width: 820px) {
	.main-nav ul{
        color: white;
    }
}
.link:hover {
    color: white;
    cursor: pointer;
  }

.link{
    text-decoration: none;
    color: black;
}
.swiper-container {
    width: 100%;
    height: 100%;
  }

  .swiper-slide {
    text-align: center;
    font-size: 18px;
    background: rgba(255, 255, 255, 0);

    /* Center slide text vertically */
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: contain;
  }

